import React, { Component } from 'react';
import { MDBCarousel, MDBCarouselCaption, MDBCarouselInner, MDBCarouselItem, MDBView, MDBMask, MDBContainer } from "mdbreact";
import './style.css';
import destination_dining from './images/destination_dining.jpg';
import valentine from './images/valentine.jpg';
import su_hiring from './images/LandingEmploymentPage.jpg';
import covid from './images/covid_signs.jpg';
import diningguide from './images/diningguide.jpg';

class SlideShow extends Component {

    render() {
		const imgs = [null, destination_dining, valentine, su_hiring, covid, diningguide];
		const urls = [null, 'https://qrco.de/destinationdining', '/valendine/index.php', '/talentjobs/index.php', '', '/diningguide/index.php'];
        const alts = [null, 'Destination Dining', 'Valentine Dinner', 'Student Unions Hiring', '', 'Dining Guide'];

        return(
            <MDBContainer className="p-0">
                <MDBCarousel activeItem={1} length={imgs.length-1} showControls={true} showIndicators={true} interval={15000} className="z-depth-1">
                    <MDBCarouselInner>

                        {
                            imgs.map((img, index) => {
                                if (img != null){
                                    return (
                                        <MDBCarouselItem itemId={index}>
                                            <MDBView>
                                                <a href={urls[index]}><img className="d-block w-100" src={img} alt={alts[index]} /></a>
                                                {/* <MDBMask overlay="black-light" /> */}
                                            </MDBView>
                                            <MDBCarouselCaption>
                                                {/* <h3 className="h3-responsive">Light mask</h3>
                                                <p>First text</p> */}
                                            </MDBCarouselCaption>
                                        </MDBCarouselItem>
                                    )
                                }
                                else {
                                    return null
                                }
                            })
                        }

                    </MDBCarouselInner>
                </MDBCarousel>
            </MDBContainer>
        );
    }
}

export default SlideShow;